import { ref } from 'vue';
import { getCurrentInstance } from "vue";
export default {
  props: ["tit", "money", "ass", "tab", "src"],

  setup() {
    const value = ref(1);
    let {
      proxy
    } = getCurrentInstance();
    const img = proxy.$route.query.src;
    return {
      value,
      img
    };
  },

  methods: {
    off() {
      // console.log(this.$route)
      this.$router.push({
        path: "/"
      });
    },

    order() {
      let obj = this.$route.query;
      obj.num = this.value;
      this.$store.commit("confirm", obj);
      this.$router.push({
        path: "/order"
      });
    },

    storage() {
      // console.log(this.value)
      // console.log(this.$route.query)
      let obj = this.$route.query;
      obj.num = this.value;
      let data = this.$store.state.data;

      if (data[0]) {
        let bool = false;
        data.some(function (val) {
          if (val.src == obj.src) {
            bool = false;
            console.log(0);
            val.num = val.num + obj.num >= 10 ? 10 : val.num + obj.num;
            return true;
          } else {
            bool = true;
            console.log(1);
            return false;
          }
        });

        if (bool) {
          this.$store.commit("add", obj);
        }
      } else {
        this.$store.commit("add", obj);
      }

      this.$toast({
        message: `<div style="position: fixed;top:400px;left:275px;font-size:36px;background-color: #4A4A4B;color:#fff;border-radius:5px;"><i class="van-icon van-icon-success" style="text-align: center;padding-top:50px;"><p style="padding:20px 38px 60px 38px">添加成功</p></div>`,
        type: 'html'
      });
      console.log(obj);
    }

  },

  created() {// console.log(this.$store.state.data[0])
    // console.log(this.$store.state.data)
  }

};